body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
	height: 70vh;
	padding-left: 14em;
	display: -webkit-flex;
	display: flex;
    -webkit-align-items: center;
            align-items: center;
    font-family: 'Josefin Sans', sans-serif !important;
}

.centerButton {
	display: -webkit-flex;
	display: flex;
	
}

@media only screen and (max-width: 600px) {
  .container {
	width: 90%;
	height: 80vh;
	padding-left: 1.8em !important;
	margin-bottom: 5em !important;
	}
}

@media only screen and (max-width: 400px) {
	.container {
	  height: 90vh;
	  width: 90%;
	  }
  }

.container h1 {
	font: 700 70px 'Dancing Script', cursive !important;
	/* margin-top: -10px; */
}

@media only screen and (max-width: 800px) {
  .container h1 {
	font: 700 60px 'Dancing Script', cursive !important;
	}
}


.animate {
	-webkit-animation-duration: 1s;
	animation-duration: 1s;
	-webkit-animation-fill-mode: both;
	animation-fill-mode: both;
  }
  
  /*=== FADE IN  ===*/
  @-webkit-keyframes fadeIn {
	from {
	  opacity: 0;
	}
  
	to {
	  opacity: 1;
	}
  }
  @keyframes fadeIn {
	from {
	  opacity: 0;
	}
  
	to {
	  opacity: 1;
	}
  }
  .fadeIn {
	  -webkit-animation-name: fadeIn;
	animation-name: fadeIn;
  }
  
  .one {
  -webkit-animation-delay: 0.5s;
  animation-delay: 0.5s;
  }
  
  .two {
  -webkit-animation-delay: 1s;
  animation-delay: 1s;
  }
  
  .three {
  -webkit-animation-delay: 1.5s;
  animation-delay: 1.5s;
  }
  
  .four {
  -webkit-animation-delay: 2s;
  animation-delay: 2s;
  }

  .five {
	-webkit-animation-delay: 2.5s;
	animation-delay: 2.5s;
	}

.six {
	-webkit-animation-delay: 3s;
	animation-delay: 3s;
	}
.grow { 
	transition: all .2s ease-in-out; 
	
}

.grow:hover { 
	-webkit-transform: scale(1.1); 
	        transform: scale(1.1); 
}

.grow:hover .items,
.grow:focus .items {
  display: block;
}

.grow:hover .items2,
.grow:focus .items2 {
  display: block;
}

.textover {
  color: white; /* Good thing we set a fallback color! */
}

.items {
    display: none;
    font: 700 60px 'Dancing Script', cursive !important;
	  color: white;
	  text-shadow: 3px 3px 0px rgba(0,0,0,0.6), 3px 3px 0px rgba(0,0,0,0.6) !important;
    text-align: center;
    margin: auto;
    position: absolute;
    top: 45%;
  	left: 50%;
  	-webkit-transform: translate(-50%, -50%);
  	        transform: translate(-50%, -50%);
    cursor: pointer;
}

.items2 {
  display: none;
  font: 700 30px 'Dancing Script', cursive !important;
  color: white;
  text-shadow: 2px 2px 0px rgba(0,0,0,0.6), 2px 2px 0px rgba(0,0,0,0.6) !important;
  text-align: center;
  margin: auto;
  position: absolute;
  top: 60%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  cursor: pointer;
}

 @media only screen and (max-width: 900px) {
  .items {
    display: block;
  }
  
  .items2 {
    display: block;
	}
}


.menuLink {
    display: inline-block;
}

.menuLink:after {
    content: '';
    display: block;
    width: 0;
    height: 2px;
    background:#c71585;
    transition: width .3s;
}

.menuLink:hover::after {
    width: 100%;
    transition: width .3s;
}
